import axios from 'axios'
import conn_util from '@/common/es/conn_util'

export function net( config) {
  
  const instance = axios.create({
    timeout: 12000
  })

  // 
  instance.defaults.retry = 2
  instance.defaults.retryDelay = 2000

  // 
  instance.interceptors.request.use(config => {
    // 
    // 1.  config
    // 2.  Loading
    // 3.  token
    config.auth = conn_util.getAuth()

    return config
  }, err => {
    // 
    console.log(err)
  })

  instance.interceptors.response.use(res => {
    return res.data
  }, err => {
    // 
    console.log('!!!')
    console.log(err)
    throw err
  })

  return instance(config)
  
}


